<template>
  <div class="list-box">
    <transition-group :class="['list', { none: !list.length, show: isShowAll }]" tag="div" name="todo-list">
      <div class="item grid" v-for="(item, inx) in list" :key="item.id" draggable @dragstart="handleDrag($event, inx)" @dragend="handleDrag($event)" @dragover.prevent="handleDragOver" @drop="handleDrop($event, inx)">
        <div :class="['item-text', { finish: item.status == 2 }]" :title="item.value">{{ item.value }}</div>
        <div>
          <dw-button type="warning" v-if="item.status == 0" waiting @click="updateRow(inx, 1)">执行中</dw-button>
          <dw-button type="primary" v-if="item.status == 1" @click="updateRow(inx, 0)">去执行</dw-button>
        </div>
        <div>
          <dw-button type="success" v-if="item.status != 2" @click="updateRow(inx, 2)">完成</dw-button>
        </div>
        <div>
          <dw-button type="danger" @click="deleteRow(inx)">删除</dw-button>
        </div>
      </div>
    </transition-group>
    <div>
      <dw-button type="info" class="pack-btn" :disabled="list.length <= 3" @click="packUp()">{{ isShowAll ? "收起" : "展开" }}</dw-button>
    </div>
  </div>
</template>

<script>
export default {
  props: ["list"],
  data() {
    return {
      isShowAll: true,
      dragging: null,
    };
  },

  methods: {
    updateRow(inx, status) {
      // this.isShowAll = false;
      this.$emit("update-row", inx, status);
    },
    deleteRow(inx) {
      this.$emit("delete-row", inx);
      if (this.list.length <= 3) {
        // 跟着动画一起变
        setTimeout(() => {
          this.isShowAll = true;
        }, 500);
      }
    },
    // 展开
    packUp() {
      if (this.list.length <= 3) return;
      this.isShowAll = !this.isShowAll;
    },
    handleDrag(e, inx = null) {
      this.dragging = inx;
    },
    //首先把div变成可以放置的元素，即重写dragenter/dragover
    handleDragOver(e) {
      e.dataTransfer.dropEffect = "move"; //在dragenter中针对放置目标来设置!
    },
    // 拖拽移动过程
    handleDragEnter(e) {},
    // 鼠标松开
    handleDrop(e, inx) {
      //为需要移动的元素设置dragstart事件
      e.dataTransfer.effectAllowed = "move";
      // 当前移动的状态
      const curStatus = this.list[this.dragging].status;
      if (!curStatus) return;
      // 限制范围 去执行（不能高于正在执行 不能低于已完成） 已完成（不能高于去执行）
      const doingInx = this.list[0].status ? 0 : 1;
      const isFinishedNum = this.list.filter(({ status }) => status == 2).length;
      const finishInx = this.list.length - 1 - isFinishedNum;
      // console.log("doingInx", doingInx, "finishInx", finishInx);
      if (curStatus == 1) {
        // 去执行的限制
        if (inx < doingInx) inx = doingInx;
        else if (inx > finishInx) inx = finishInx;
      } else {
        // 已完成的限制
        if (inx < finishInx + 1) inx = finishInx + 1;
      }
      if (inx === this.dragging) return;
      this.$emit("switch-row", this.dragging, inx);
    },
  },
};
</script>

<style scoped>
.list-box {
  margin-top: 50px;
  width: 100%;
}
.list {
  width: 100%;
  max-height: 150px;
  overflow: scroll;
  transition: max-height ease 0.5s;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}
/* 隐藏scroll滚动条但不影响滚动功能 */
.list::-webkit-scrollbar {
  display: none;
}
.none {
  border-top: 1px solid #d9d9d9;
  /* min-height: 50px; */
}
.item {
  height: 50px;
  grid-template: 1fr/58% 16% 13% 13%;
  border-top: 1px solid #d9d9d9;
  padding: 0 10px;
  align-items: center;
  box-sizing: border-box;
}
.item-text {
  margin-right: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.finish {
  text-decoration: line-through;
}

.pack-btn {
  width: 100%;
  border-radius: 0 0 4px 4px;
}
.show {
  max-height: 62vh;
}
.todo-list-move {
  transition: transform 1s;
}
/* 离开动画 */
.todo-list-leave-active {
  animation: leave 0.5s ease-out;
}
@keyframes leave {
  to {
    opacity: 0;
    z-index: -1;
    transform: translateY(100%);
  }
}
</style>
